<template>
  <transition name="upper">
    <div class="detail" v-show="$store.state.show">
      <header>
        <i class="icon-arrow"
           @click="$store.dispatch('hide')">
          &#xe60c;
        </i>
        正在播放
        <i class="icon-share">&#xe6f0;</i>
      </header>
      <div class="detail-content">
        <div :class="['disc-pic',{'stop': !$store.state.playing}]">
          <img src="static/images/thum.jpg">
        </div>
      </div>
      <div class="lyc">（暂无歌词!）</div>
      <footer class="operators">
        <div class="progress-box">
          <span>{{currentTimeShow}}</span>
          <div class="progress" ref="progress">
            <div class="progress-bar"
                 :style="{width:percent}">
            </div>
            <div class="control-ball" @touchstart="speed($event, 'start')" @touchmove="speed($event, 'move')" @touchend="speed($event, 'end')"></div>
          </div>
          <span>{{durationShow}}</span>
        </div>
        <div class="control-bar">
          <i class="icon-loop">&#xe69c;</i>
          <i class="icon-prev"
             @click="control('prev')">
            &#xe6e1;
          </i>
          <i :class="!$store.state.playing?'icon-play': 'icon-stop'"
             @click="control('play')">
          </i>
          <i class="icon-next"
             @click="control('next')">
            &#xe718;
          </i>
          <i class="icon-list"
             @click="songList = !songList">
            &#xe6be;
          </i>
        </div>
        <audio ref="music" src="static/music/song1.mp3"
               @timeupdate="progress($event)">
        </audio>
      </footer>
      <transition name="scale">
        <div class="song-list" v-show="songList">
          <div @click="select(0)">song1</div>
          <div @click="select(1)">song2</div>
          <div @click="select(2)">song3</div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
    export default {
      name: "music-detail",
      data(){
        return{
          play: true,                   //播放状态
          duration: 0,                  //总时长
          durationShow: '',             //显示总时长（格式转换）
          currentTime: 0,               //已播放时长
          currentTimeShow: '',          //显示已播放时长
          percent: 0,
          songs:[
            'static/music/song1.mp3',
            'static/music/song2.mp3',
            'static/music/song3.mp3',
          ],
          index: 0,
          testDataStr: '123',
          testDataNum: 110,
          songList: false,
          touchStart: 0,
          auto: true                    //进度条自动更新
        }
      },
      mounted(){
        this.$store.dispatch('setData',this.$refs.music);
        this.$store.getters.playState;
      },
      methods:{
        control(type){
          switch(type){
            case 'play': {
              if(this.$store.state.playing){
                this.$refs.music.pause();
              }
              else{
                this.$refs.music.play();
              }
              this.$store.dispatch('playing');
              break;
            }
            case 'next': this.next(); break;
            case 'prev': this.prev(); break;
          }
        },
        progress(event){
          if(!this.auto)
            return;
          this.duration = event.currentTarget.duration;
          this.currentTime = event.currentTarget.currentTime;
          this.percent = this.currentTime/this.duration*100+'%';
          this.durationShow = this.timeTransform(this.duration);
          this.currentTimeShow = this.timeTransform(this.currentTime);
          this.$refs.music.addEventListener('ended',()=>{
            this.next();
          });
        },
        timeTransform(time){
          let newTime = {};
          newTime.min = time < 600?`0${parseInt(time/60)}` : parseInt(time/60);
          newTime.sec = Math.ceil(time % 60) < 10?`0${Math.ceil(time % 60)}` : Math.ceil(time % 60);

          return `${newTime.min}:${newTime.sec}`;
        },
        next(){
          let event = this.$refs.music;
          this.index++;
          if(this.index === this.songs.length)
            this.index = 0;
          event.src = this.songs[this.index];
          event.pause();
          event.play();
        },
        prev(){
          let event = this.$refs.music;
          this.index--;
          if(this.index < 0)
            this.index = this.songs.length - 1;
          event.src = this.songs[this.index];
          event.pause();
          event.play();
        },
        select(i){
          let event = this.$refs.music;
          if(i >= 0&& i < this.songs.length){
            this.index = i;
            event.src = this.songs[this.index];
            event.pause();
            event.play();
            this.songList = false;
            this.play = false;
          }
        },
        speed(e, action){
          if(action === 'start'){
            this.auto = false;
            this.touchStart = e.changedTouches[0].pageX;
          }
          else if(action === 'end'){
            this.$refs.music.currentTime = this.$refs.music.duration * this.percent.replace('%', '')/100;
            this.auto = true;
          }
          else{
            let total = this.$refs.progress.offsetWidth;
            let change = e.changedTouches[0].pageX - this.touchStart;
            this.touchStart = e.changedTouches[0].pageX;
            let changePercent = change/total*100;
            this.percent = parseFloat(this.percent.toString().replace('%', '')) + changePercent +'%';
          }
        }
      }
    }
</script>

<style scoped>
  .detail{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 999;
    background-color: #fff;
  }
  .detail>header{
    height: 2.5rem;
    border-bottom: 1px solid #EEEEEE;
    background-color: #c62f2f;
    color: #fff;
    font-size: 14px;
    line-height: 2.5rem;
  }
  .icon-arrow{
    display: inline-block;
    transform: rotate(90deg);
    font-size: 1rem;
    margin: 0 .5rem;
  }
  .icon-share{
    float: right;
    font-size: 1rem;
    margin-right: .8rem;
  }
  .disc-pic{
    width: 10rem;
    height: 10rem;
    margin: 3rem auto 0;
    border-radius: 50%;
    overflow: hidden;
    animation: Rotate 8s linear infinite;
  }
  .disc-pic>img{
    width: 100%;
    height: 100%;
  }
  .stop{
    animation-play-state:paused;
  }
  @keyframes Rotate {
    0% {transform: rotate(0)}
    100% {transform: rotate(360deg)}
  }
  .operators{
    position: fixed;
    width: 100%;
    height: 6rem;
    bottom: 0;
    left: 0;
  }
  .control-bar{
    width: 80%;
    height: 5rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 1.2rem;
  }
  .scale-enter-to, .scale-leave-to{
    transition: all .2s;
  }
  .scale-enter, .scale-leave-to{
    transform: scale3d(.7,.7,0);
    transform-origin: 80% 110%;
    opacity: 0;
    height: 0 !important;
  }
  .icon-play::before{
    content: '\e624';
    font-size: 1.2rem;
    font-family: inherit;
    animation: Scale1 .2s ease;
  }
  .icon-stop::before{
    content: '\e634';
    font-size: 1.2rem;
    font-family: inherit;
    animation: Scale .2s ease;
  }
  @keyframes Scale{
    0% {font-size: 1.2rem}
    50% {font-size: 2rem;}
    100% {font-size: 1.2rem;}
  }
  @keyframes Scale1{
    0% {font-size: 1.2rem}
    50% {font-size: 2rem;}
    100% {font-size: 1.2rem;}
  }
  .progress{
    width: calc(100% - 6rem);
    height: 4px;
    border: 1px solid #dcdcdc;
    border-radius: 4px;
    display: inline-block;
    margin: 0 5px;
  }
  .progress-bar{
    height: 4px;
    float: left;
    border-radius: 2px;
    background-color: #59a7f5;
  }
  .progress-box{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .lyc{
    margin-top: 2rem;
    text-align: center;
  }
  .song-list{
    position: fixed;
    background-color: rgba(69, 69, 69, .5);
    color: #fff;
    width: 9rem;
    height: 12rem;
    right: 1.5rem;
    bottom: 4rem;
    border-radius: 5px;
    padding: .8rem;
    box-sizing: border-box;
  }
  .song-list::before{
    content:'';
    border-top:12px solid rgba(69, 69, 69, .5);
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    position: absolute;
    bottom: -12px;
    right: 25px;
  }
  .song-list>div{
    line-height: 1.4rem;
    text-align: center;
    border-bottom: 1px solid #c2c2c2;
  }
  .control-ball{
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #59a7f5;
    border: 5px solid #dcdcdc;
    float: left;
    position: relative;
    top: -5px;
    left: -5px;
  }
</style>



